.container {
	padding-left: 100px;
	padding-right: 100px; }
header {
	position: relative;
	height: 50px; }
nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 2; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	&_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300;
		text-decoration: none; }
	&_item {
		border-right: 2px dotted black;
		padding-right: 28px;
		&:last-child {
			border-right: none; } } }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0,0,0, .6);
	&_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }
	&_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }
	&_call {
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right; }
	&_phone {
		display: block;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-align: right;
		text-decoration: none; }
	&_btn {
		margin-top: 16px;
		width: 178px;
		height: 52px;
		background-image: linear-gradient(0.0deg, #1eacc7 0%, #4ce2ff 100%);
		border: none;
		box-shadow: inset 0px -3px 0px 0px rgba(0,0,0,0.11);
		border-radius: 4px;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		text-shadow: 1px 1px 0px rgba(0,0,0,0.2);
		color: #ffffff;
		&:hover {
			@include btn_hover; } } }

.hamburger {
	display: none; }

.promo {
	min-height: 800px;
	background: url('../img/main_bg.png') center center/cover no-repeat;
	padding: 130px 0 132px 0;
	&_header {
		color: #ffffff;
		font-weight: 600;
		text-transform: uppercase;
		font-size: 60px;
		margin: 0;
		text-align: center; }
	&_subheader {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 36px;
		margin: 0;
		text-align: center; }
	&_text {
		font-size: 18px;
		font-weight: 400;
		color: #ffffff;
		margin-top: 23px;
		text-align: center; }
	&_btn {
		display: block;
		margin: 90px auto 0 auto;
		width: 265px;
		height: 69px;
		background-image: linear-gradient(0.0deg, #1eacc7 0%, #4ce2ff 100%);
		border: none;
		border-radius: 4px;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		text-shadow: 1px 1px 0px rgba(0,0,0,0.2);
		color: #ffffff;
		&:hover {
			@include btn_hover; } } }
.reasons {
	padding: 71px 0 65px 0;
	&_block {
		display: flex;
		justify-content: center;
		align-items: start;
		margin-top: 35px;
		.reason_descr {
			width: 343px;
			margin-left: 26px;
			.reasons_subheader {
				color: #1eacc7;
				font-size: 17px;
				font-weight: 400; }
			.reasons_text {
				color: #202020;
				font-size: 15px;
				font-weight: 300;
				text-align: justify; } } } }
.label {
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center; }
.title {
	margin-bottom: 0;
	margin-top: 30px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center; }
.subtitle {
	color: #222222;
	font-size: 17px;
	font-weight: 400;
	text-align: center; }
.mobile {
	background: url('../img/bg_cells.png') center center/cover no-repeat;
	min-height: 864px;
	padding: 48px 0 60px 0;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin-bottom: 19px;
		padding-top: 60px;
		&_1 {
			background: url('../img/mobile/app1.png') center center/cover no-repeat; }
		&_2 {
			background: url('../img/mobile/app2.png') center center/cover no-repeat; }
		&_3 {
			background: url('../img/mobile/app3.png') center center/cover no-repeat; }
		&_4 {
			background: url('../img/mobile/app4.png') center center/cover no-repeat; }
		&_5 {
			background: url('../img/mobile/app5.png') center center/cover no-repeat; }
		&_6 {
			background: url('../img/mobile/app6.png') center center/cover no-repeat; }
		&_subtitle {
			font-size: 24px;
			font-weight: 400;
			text-transform: uppercase;
			color: #ffffff;
			text-align: center; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			border-radius: 100%;
			cursor: pointer;
			&::before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;
				background-color: $main_color; }
			&::after {
				content: '';
				display: block;
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				width: 2px;
				height: 14px;
				background-color: $main_color; }
			&:hover {
				&::before, &::after {
					background-color: $second-color; } } } } }

.choice {
	.black {
		min-height: 499px;
		padding-top: 50px;
		background-color: #070716;
		position: relative;
		.label {
			background-color: #ffffff;
			color: #070716; }
		.title {
			color: #ffffff; }
		.subtitle {
			color: #ffffff; }
		.car {
			position: absolute;
			top: 232px;
			left: 50%;
			transform: translateX(-50%); }
		.xs-visible-car-text {
    		display: none; } }
	.white {
		min-height: 330px;
		background: url('../img/bg_cells.png') center center/cover no-repeat;
		padding-top: 217px;
		.car_text {
			font-size: 17px;
			font-weight: 300;
			color: #222222;
			text-align: center; } } }

.conditions {
	background-color: #fff;
	padding: 50px 0;
	&_block {
		width: 500px;
		margin: 30px 0; }
	&_title {
		font-size: 22px;
		font-weight: 400;
		color: #1ca8c3; }
	&_list {
		list-style-type: none;
		padding-left: 30px;
		margin-top: 10px;
		font-size: 17px;
		font-weight: 300;
		color: #222222;
		li {
			margin-bottom: 5px;
			// position: relative
			&::before {
				content: '';
				display: inline-block;
				vertical-align: middle;
				transform: translateX(-10px);
				// display: block
				// position: absolute
				// left: -32px
				width: 21px;
				height: 20px;
				background: url('../icons/check_marks/blue_mark.png') center center/cover no-repeat; } } }
	&_descr {
		font-size: 17px;
		font-weight: 300;
		color: #1ca8c3; }
	.requires {
		.conditions_title {
			color: #ff5656; }
		.conditions_list {
			li {
				&::before {
					background: url('../icons/check_marks/red_mark.png') center center/cover no-repeat; } } } } }

.world {
	min-height: 717px;
	background: url('../img/world_map.png') center center no-repeat;
	// background-color: #080816
	background-color: #070716;
	padding: 49px 0;
	position: relative;
	.world_label {
		width: 76px;
		height: 24px;
		background-color: #ffffff;
		text-align: center;
		margin: 0 auto; }
	.title, .subtitle {
		color: #fff; }
	.cities {
		position: absolute;
		bottom: 49px;
		right: 50%;
		transform: translateX(50%);
		font-size: 15px;
		font-weight: 400;
		text-transform: uppercase;
		color: #1fbad6;
		text-decoration: none; } }
footer {
	min-height: 156px;
	padding-top: 14px;
	position: relative;
	.divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	// .footer_wrapper
	.footer_links {
		width: 330px;
		margin: 0 auto;
		margin-top: 32px;
		text-align: center;
		a {
			text-decoration: none;
			font-weight: 300;
			text-transform: uppercase; }
		&_main {
			display: flex;
			justify-content: space-between; }
		&_main a {
			font-size: 15px;
			color: #070716;
 }			// margin-right: 45px
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 6px;
			a {
				font-size: 11px;
				color: #999999; } }
		&_lang {
			// margin: 19px 0 39px 0
			margin-top: 19px;
			padding-bottom: 39px;
			a {
				font-size: 11px;
				font-weight: 400;
				text-transform: uppercase;
				color: #1fbad6; } } }

	.footer_app {
		position: absolute;
		right: 23px;
		top: 52px; }

	.footer_icons {
		position: absolute;
		left: 23px;
		top: 52px;
		width: 155px;
		display: flex;
		justify-content: space-between;
		&_circle {
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			i {
				color: #fff; } } }
	.bottom {
		width: 100%;
		height: 8px;
		background-color: #070716;
		position: absolute;
		bottom: 0; } }

.xs-visible {
    display: none; }
