@media (max-width: 768px) {
    .container {
        padding-left: 0;
        padding-right: 0; }
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, .8);
        transition: 0.6s all;
        &_active {
            left: 0; }
        &_item {
            padding-right: 0;
            border: none;
            margin-top: 12px; }
        &_link {
            font-size: 17px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &_phone {
            display: block;
            font-size: 16px;
            font-weight: 400;
            text-transform: uppercase;
            color: #ffffff;
            text-align: center;
            margin-top: 10px; }
        &_btn {
            display: block;
            width: 200px;
            height: 40px;
            background-image: linear-gradient(0.0deg, #1eacc7 0%, #4ce2ff 100%);
            border: none;
            box-shadow: inset 0px -3px 0px 0px rgba(0,0,0,0.11);
            font-size: 14px;
            margin: 0 auto;
            margin-top: 6px; } }
    .promo {
        &_header {
            font-size: 40px; }
        &_subheader {
            font-size: 26px; } }
    .title {
        font-size: 30px; }
    .mobile {
        .title {
            font-size: 30px; }
        &_item {
            &_subtitle {
                font-size: 18px; } } }
    .choice {
        .title {
            font-size: 30px; }
        padding-top: 40px;
        min-height: 600px;
        // .car
 }        //     width: 690px
    .conditions {
        padding: 20px 0;
        .title {
            font-size: 25px; }
        &_block {
            width: 440px;
            margin: 20px 0 0 40px; }
        &_list {
            margin-bottom: 0; } }
    .world {
        min-height: 450px;
        .title {
            font-size: 30px; } } }

